/* src/app/product-list/product-list.css */
.product-grid {
  font-family:
    "Segoe UI",
    Tahoma,
    Geneva,
    Verdana,
    sans-serif;
  display: grid;
  gap: 10px;
  padding: 16px;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
[ngIf=loading] {
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  margin: 20px 0;
}
